$dark: rgb(98, 32, 20);
$light: rgb(168, 54, 34);
$highlight: rgb(195, 63, 40);

$dark: rgb(20, 69, 98);
$light: rgb(34, 108, 168);
$highlight: rgb(40, 146, 195);

$headerDark:rgb(22, 22, 22);
$headerMedium:rgb(29, 29, 29);
$headerLight:rgb(62, 62, 62);
$headerHighlight:rgb(90, 91, 91);

$depth: 6px;

.nav{
    transform: rotate('45 deg');
    position: relative;
    padding-left: $depth;
    display: flex;
    flex-direction: column;
    justify-content: start;
    background:linear-gradient(to right, $dark, $light $depth*2);
    -webkit-clip-path: polygon(0 0, 0 100%, 100% 100%, 100% 25%, 75% 0);
    clip-path: polygon(0 0, calc(100% + $depth*2) 0, calc(100% + $depth*2) 100%, $depth 100%, 0 calc(100% - $depth));
    &__spacer{
        box-shadow: -1px 0 0 rgba(255, 255, 255, 0.05);
        background:radial-gradient($light ,$dark);
        background-size: 200% 200%;
        background-position: 12% 0%;
    }
    &__section{
        

        box-shadow: -1px 0 0 rgba(255, 255, 255, 0.05);
        background:radial-gradient($light ,$dark);
    }
    &__header{
        position: relative;
        //border-top: 1px solid $headerHighlight;
        width: calc(100% + ($depth*1.25));
        box-shadow: -1px -2px 6px rgba(0, 0, 0, 0.541), inset 0 1px 0 rgb(255, 255, 255);


    }
    &__title{
        position: relative;
        background: radial-gradient(at top,$headerLight,$headerMedium 80%,$headerDark);
        background-size: 150% 100%;
        background-position: 50% 0%;
        padding:0.5rem;
        box-shadow:inset 0 1px 0 rgba(255, 255, 255, 0.137);
    }
    &__headerTail{
        position: absolute;
        background-color: $headerMedium;
        right:100%;
        width: $depth;
        height: 100%;
        transform: skewY(.785398rad);
        transform-origin: right;
        box-shadow: 0px -3px 6px rgba(0, 0, 0, 0.89);
    }
    &__headerTailend{
        position: absolute;
        background-color: $headerMedium;
        left:calc(100% - ($depth*2));
        width: ($depth*2);
        height: 100%;
        transform: skewY(.785398rad);
        transform-origin: right;
        z-index: -1;
    }
    &__sectionContent{
        //border-left: 1px solid $light;
        
        padding: 1rem;
    }
    &__spacerShadow{
        background:radial-gradient($dark ,$dark);
    }


    
}
