}



.nav {
  transform: rotate("45 deg");
  -webkit-clip-path: polygon(0 0, 0 100%, 100% 100%, 100% 25%, 75% 0);
  clip-path: polygon(0 0, calc(100% + 12px) 0, calc(100% + 12px) 100%, 6px 100%, 0 calc(100% - 6px));
  background: linear-gradient(to right, #144562, #226ca8 12px);
  flex-direction: column;
  justify-content: start;
  padding-left: 6px;
  display: flex;
  position: relative;
}

.nav__spacer {
  background: radial-gradient(#226ca8, #144562) 12% 0 / 200% 200%;
  box-shadow: -1px 0 #ffffff0d;
}

.nav__section {
  background: radial-gradient(#226ca8, #144562);
  box-shadow: -1px 0 #ffffff0d;
}

.nav__header {
  width: calc(100% + 7.5px);
  position: relative;
  box-shadow: -1px -2px 6px #0000008a, inset 0 1px #fff;
}

.nav__title {
  background: radial-gradient(at top, #3e3e3e, #1d1d1d 80%, #161616) 50% 0 / 150% 100%;
  padding: .5rem;
  position: relative;
  box-shadow: inset 0 1px #ffffff23;
}

.nav__headerTail {
  width: 6px;
  height: 100%;
  transform-origin: 100%;
  background-color: #1d1d1d;
  position: absolute;
  right: 100%;
  transform: skewY(45deg);
  box-shadow: 0 -3px 6px #000000e3;
}

.nav__headerTailend {
  width: 12px;
  height: 100%;
  transform-origin: 100%;
  z-index: -1;
  background-color: #1d1d1d;
  position: absolute;
  left: calc(100% - 12px);
  transform: skewY(45deg);
}

.nav__sectionContent {
  padding: 1rem;
}

.nav__spacerShadow {
  background: radial-gradient(#144562, #144562);
}

